/* ----- ANIMATIONS ----- */
@-webkit-keyframes fadeInDesktop {
    0% { padding-top: 25%; opacity: 0; }
    100% { padding-top: 2%; opacity: 1; }
}
@keyframes fadeInDesktop {
    0% { padding-top: 25%; opacity: 0; }
    100% { padding-top: 2%; opacity: 1; }
}

@-webkit-keyframes fadeInPhone {
    0% { padding-top: 50%; opacity: 0; }
    100% { padding-top: 20%; opacity: 1; }
}

@keyframes fadeInPhone {
    0% { padding-top: 50%; opacity: 0; }
    100% { padding-top: 20%; opacity: 1; }
}


/* ----- LANDING PAGE ----- */
#landing_page {
    height: 85vh;
    background: url(/static/media/background.24f0eaed.jpg);
    background-repeat: no-repeat;
    background-position: top right;
    background-size: cover;
    background-attachment: fixed;
}

#landing_page_content {
    padding: 2% 0 0 7%;
    -webkit-animation-name: fadeInDesktop;
            animation-name: fadeInDesktop;
    -webkit-animation-duration: 1.7s;
            animation-duration: 1.7s;
}

#landing_page_content_title {
    width: 70%;
    color: purple;

    font-weight: bold;
    font-size: 5vmax;
    line-height: 6vmax;
}

#landing_page_content_text {
    width: 60%;
    color: #4C535B;
    font-size: 2vmax;
}


.home_text {
    margin: 3% 5%;
    color: #4B545A;
    
    font-size: 2vmax;
    font-style: italic;
}

@media (max-width: 500px) {
    .home_text { margin: 10%; }
    #landing_page { background-position: top left; }
    #landing_page_content { padding: 20% 5% 0; -webkit-animation-name: fadeInPhone; animation-name: fadeInPhone; }
    #landing_page_content_title { text-align: center; margin: auto; }
    #landing_page_content_text { font-size: 1.25em; width: unset; text-align: center; }
    .home_text { font-size: 1.75vmax; }
    #logo_img { width: 50%; display: block; margin: auto; }
}
@media (max-width: 900px) and (orientation: landscape)  {
    #landing_page_content_title { font-size: 3em; }
}
